@import url(https://fonts.googleapis.com/css2?family=Dosis);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.mainBox {
	/* display: flex; */
	padding-top: 8vh;
	min-height: 100vh;
	background-color: #212121;
	align-items: center;
	text-align: center;
	color: #fff;
	width: 100vw;
}

.subBox {
	margin: auto;
	/* max-width: 350px; */
}

.companyName {
	font-size: 35px;
	font-family: 'Dosis';
	font-weight: 800;
}

.comingSoon {
	margin-top: 5vh;
	font-size: 40px;
	font-family: 'Dosis';
	font-weight: 700;
}
.timerBox {
	margin: auto;
	margin-top: 20vh;
	max-width: 600px;
}
.timerElement {
	font-size: 35px;
	font-family: 'Dosis';
	font-weight: 700;
